<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'

export default {
  name: 'CssGeneratorView',
  metaInfo: {
    // æ ‡é¢˜ï¼šçªå‡ºæ ¸å¿ƒåŠŸèƒ½ï¼ˆä»£ç ç”Ÿæˆï¼‰ã€å¯è§†åŒ–ç‰¹æ€§å’Œæ¶µç›–çš„å…³é”® CSS æ•ˆæœ
    title: 'CSS ç”Ÿæˆå™¨ - åœ¨çº¿å¯è§†åŒ–æ¸å˜ã€é˜´å½±ã€è¾¹æ¡†ã€åŠ¨ç”»ä»£ç ç”Ÿæˆå·¥å…· | å…è´¹å‰ç«¯å¼€å‘è¾…åŠ©',

    meta: [
      // æè¿°ï¼šè¯¦ç»†è¯´æ˜å·¥å…·çš„äº¤äº’æ–¹å¼ï¼ˆå¯è§†åŒ–ï¼‰ï¼Œä»¥åŠå®ƒå¦‚ä½•å¸®åŠ©å¼€å‘è€…æé«˜æ•ˆç‡å’Œç”Ÿæˆä¸“ä¸šçº§æ•ˆæœ
      {
        name: 'description',
        content: 'å…è´¹åœ¨çº¿ CSS ä»£ç ç”Ÿæˆå™¨ã€‚é€šè¿‡å¯è§†åŒ–ç•Œé¢å®æ—¶è°ƒæ•´å‚æ•°ï¼Œä¸€é”®ç”Ÿæˆ box-shadow é˜´å½±ã€background-gradient æ¸å˜ã€border è¾¹æ¡†å’Œå…³é”®å¸§åŠ¨ç”»ç­‰ä¸“ä¸š CSS3 ä»£ç ï¼Œå¤§å¹…æå‡å‰ç«¯å¼€å‘æ•ˆç‡ã€‚'
      },

      // å…³é”®è¯ï¼šè¦†ç›–æ ¸å¿ƒåŠŸèƒ½ã€å…·ä½“çš„ CSS å±æ€§åç§°å’Œé€šç”¨å¼€å‘æœç´¢è¯
      {
        name: 'keywords',
        content: 'CSS ç”Ÿæˆå™¨, åœ¨çº¿ CSS3 ä»£ç ç”Ÿæˆ, å¯è§†åŒ– CSS, box-shadow ç”Ÿæˆå™¨, CSS æ¸å˜ç”Ÿæˆ, CSS åŠ¨ç”»ç”Ÿæˆ, å‰ç«¯è¾…åŠ©å·¥å…·, å…è´¹ CSS ç”Ÿæˆå™¨'
      }
    ]
  },
  data() {
    return {
      activeTab: 'gradient',
      animationKey: 0,
      
      // æ¸å˜é…ç½®
      gradient: {
        type: 'linear',
        angle: 45,
        colorStops: [
          { color: '#ff6b6b', position: 0 },
          { color: '#4ecdc4', position: 100 }
        ]
      },
      
      // é˜´å½±é…ç½®
      shadow: {
        type: 'box-shadow',
        offsetX: 5,
        offsetY: 5,
        blur: 10,
        spread: 0,
        color: 'rgba(0, 0, 0, 0.3)',
        inset: false
      },
      
      // è¾¹æ¡†é…ç½®
      border: {
        width: 2,
        style: 'solid',
        color: '#409eff',
        radius: {
          topLeft: 0,
          topRight: 0,
          bottomRight: 0,
          bottomLeft: 0
        }
      },
      
      // åŠ¨ç”»é…ç½®
      animation: {
        type: 'fadeInOut',
        duration: 1,
        easing: 'ease-in-out',
        iteration: '1',
        customIteration: 1,
        direction: 'normal'
      }
    }
  },
  computed: {
    generatedGradient() {
      const { type, angle, colorStops } = this.gradient
      const stops = colorStops
        .sort((a, b) => a.position - b.position)
        .map(stop => `${stop.color} ${stop.position}%`)
        .join(', ')
      
      if (type === 'linear') {
        return `linear-gradient(${angle}deg, ${stops})`
      } else {
        return `radial-gradient(circle, ${stops})`
      }
    },
    
    generatedShadow() {
      const { offsetX, offsetY, blur, spread, color, inset, type } = this.shadow
      
      if (type === 'box-shadow') {
        const insetStr = inset ? 'inset ' : ''
        return `${insetStr}${offsetX}px ${offsetY}px ${blur}px ${spread}px ${color}`
      } else {
        return `${offsetX}px ${offsetY}px ${blur}px ${color}`
      }
    },
    
    generatedBorder() {
      const { width, style, color } = this.border
      return `${width}px ${style} ${color}`
    },
    
    generatedBorderRadius() {
      const { topLeft, topRight, bottomRight, bottomLeft } = this.border.radius
      return `${topLeft}px ${topRight}px ${bottomRight}px ${bottomLeft}px`
    },
    
    generatedBorderCSS() {
      const border = this.generatedBorder
      const radius = this.generatedBorderRadius
      return `border: ${border};\nborder-radius: ${radius};`
    },
    
    generatedAnimation() {
      const { type, duration, easing, iteration, customIteration, direction } = this.animation
      const iterationValue = iteration === 'custom' ? customIteration : iteration
      return `${type} ${duration}s ${easing} ${iterationValue} ${direction}`
    },
    
    generatedAnimationCSS() {
      const animationName = this.animation.type
      const { duration, easing, iteration, customIteration, direction } = this.animation
      const iterationValue = iteration === 'custom' ? customIteration : iteration
      
      const keyframes = this.getKeyframes(animationName)
      
      return `/* åŠ¨ç”»å®šä¹‰ */
@keyframes ${animationName} {
${keyframes}
}

/* åº”ç”¨åŠ¨ç”» */
.animated-element {
  animation: ${animationName} ${duration}s ${easing} ${iterationValue} ${direction};
}`
    }
  },
  methods: {
    handleTabClick() {
      // åˆ‡æ¢æ ‡ç­¾é¡µæ—¶çš„å¤„ç†
    },
    
    addColorStop() {
      const lastPosition = Math.max(...this.gradient.colorStops.map(s => s.position))
      this.gradient.colorStops.push({
        color: '#ffffff',
        position: Math.min(lastPosition + 20, 100)
      })
    },
    
    removeColorStop(index) {
      this.gradient.colorStops.splice(index, 1)
    },
    
    restartAnimation() {
      this.animationKey++
    },
    
    getKeyframes(animationType) {
      const keyframes = {
        fadeInOut: `  0% { opacity: 0; }
  50% { opacity: 1; }
  100% { opacity: 0; }`,
        scale: `  0% { transform: scale(1); }
  50% { transform: scale(1.2); }
  100% { transform: scale(1); }`,
        rotate: `  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }`,
        slide: `  0% { transform: translateX(-100%); }
  100% { transform: translateX(0); }`,
        bounce: `  0%, 20%, 53%, 80%, 100% { transform: translate3d(0,0,0); }
  40%, 43% { transform: translate3d(0, -30px, 0); }
  70% { transform: translate3d(0, -15px, 0); }
  90% { transform: translate3d(0, -4px, 0); }`
      }
      
      return keyframes[animationType] || keyframes.fadeInOut
    },
    
    async copyToClipboard(text) {
      try {
        const success = await ClipboardUtil.copyText(text)
        if (success) {
          this.$message.success('CSSä»£ç å·²å¤åˆ¶åˆ°å‰ªè´´æ¿ï¼')
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥ï¼Œè¯·æ‰‹åŠ¨å¤åˆ¶ï¼')
        }
      } catch (error) {
        console.error('å¤åˆ¶å¤±è´¥:', error)
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    }
  }
}
</script>

